<template>
  <div class="row auto" style="background-color: #FFFFFF">
    <div class="col-xs-4 text-center daiban" @click = "gotopage('applytodolist')">
      <img src="../../assets/applytodo.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">报建待办</p>
    </div>
    <div class="col-xs-4 text-center yiban" @click = "gotopage('applydownlist')">
      <img src="../../assets/applydown.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">报建已办</p>
    </div>
    <div class="col-xs-4 text-center upload" @click = "gotopage('applylistupload')">
      <img src="../../assets/applylistupload.png" class="imgs" alt="">
      <p class="text-center name" style="margin: 10px 0;">批量上传</p>
    </div>
  </div>
</template>

<script>

  import co from 'co'
  import { PagedList } from 'vue-client'
  import Vue from 'vue'

  export default {
    title: '报建模块',
    data() {
      return {
        model: {}
      }
    },
    methods: {
      gotopage(param) {
        this.$goto(param)
      },
      //头部事件回掉
      back () {
        this.$back()
      },
    },
    created(){
      var result = HostApp.readLocalFile('workflow_apply.json')
      if(result.code == 500){
        this.$showMessage('获取流程配置文件失败，请联系系统管理员')
        return
      }
      var data = JSON.parse(result.data)
      console.log('前台存入缓存中的data=>' + JSON.stringify(data))
      Vue.workflow_vue = Vue.prototype.$workflow_vue = data
    }
  }

</script>
<style scoped>
  .flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .flex-between span {
    padding-right: 10px;
  }
  .flex-full {
    flex: 1;
  }
  .name{
    font: 15px PingFang-SC-Medium;
    color: #666666;
  }
  .imgs{
    width: 60px;
    margin-top: 15px;
  }
  .daiban {
    border-bottom: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .yiban{
    border-bottom: 0.5px solid #EAEAEA;
    border-right: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }
  .upload{
    border-bottom: 0.5px solid #EAEAEA;
    border-left: 0.5px solid #EAEAEA;
    height: 120px;
  }


</style>



